.p-search {
  max-width: 1290px;
  margin: 150px auto 0;
}

@media (max-width: 896px) {
  .p-search {
    margin: 100px auto 0;
  }
}

.p-search .-box-subscribe .inner {
  background: none;
}

@media (min-width: 897px) and (max-width: 1439px) {
  .c-search {
    padding: 0 30px;
  }
}

@media (max-width: 896px) {
  .c-search {
    padding: 0 30px;
  }
}

.c-search__heading {
  font-size: 2.2rem;
  color: #777;
  text-align: center;
}

@media (max-width: 896px) {
  .c-search__heading {
    font-size: 1.8rem;
  }
}

.c-search__subheading {
  font-size: 4rem;
  text-align: center;
}

@media (max-width: 896px) {
  .c-search__subheading {
    font-size: 3.2rem;
  }
}

.c-search__subheading .c-keyword {
  color: #211E1F;
}

.c-search__subheading .c-search-result-number {
  color: #FFC416;
  padding-left: 10px;
}

.c-search__list {
  max-width: 1290px;
  margin: 80px auto 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

@media (max-width: 896px) {
  .c-search__list {
    margin: 60px auto 0;
  }
}

.c-search__list-item {
  width: 31.78%;
}

@media (max-width: 896px) {
  .c-search__list-item {
    width: 100%;
  }
}

@media (min-width: 897px) {
  .c-search__list-item:nth-child(n+4) {
    margin-top: 50px;
  }
}

@media (max-width: 896px) {
  .c-search__list-item:not(:first-of-type) {
    margin-top: 50px;
  }
}

.c-search__list-link {
  color: #211E1F;
}

.c-search-card__frame {
  border-radius: 30px;
  overflow: hidden;
}

.c-search-card__image {
  max-width: 100%;
}

.c-search-card__heading {
  font-size: 2.2rem;
  margin-top: 20px;
  line-height: 1.3;
}

@media (max-width: 896px) {
  .c-search-card__heading {
    font-size: 1.8rem;
  }
}

.c-search-card__content {
  margin-top: 10px;
  font-size: 1.6rem;
  color: #777777;
}

@media (max-width: 896px) {
  .c-search-card__content {
    font-size: 1.4rem;
  }
}
